.button {
  display: block;
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: $color-tangaroa;
  background-color: $color-alice-blue;
  border: none;
  cursor: pointer;

  &--lower {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &--higher {
    padding-top: 9px;
    padding-bottom: 12px;
  }

  &:hover,
  &:focus {
    background-color: $background-solitude;
    color: $color-tangaroa;
    box-shadow: inset 0 0 0 2px $color-quartz;
  }

  &:active {
    background-color: $background-solitude;
    color: $color-alice-blue;
  }
}
